import { videoDuration } from "./video/types.js";
import { SECONDS_BETWEEN_VIDEOS } from "./playback.js";
import { equals, expect, is, test } from "../_snowpack/pkg/@benchristel/taste.js";
function gap(nextVideoId) {
  return {
    type: "nothing",
    nextVideoId
  };
}
export function LoopingChannel(name, videos) {
  const totalScheduleDuration = videos.map((v) => videoDuration(v)).reduce(add, 0) + videos.length * SECONDS_BETWEEN_VIDEOS;
  return {
    getBroadcast: (currentMillis) => {
      const clockTime = currentMillis / 1e3;
      let videoDurationSoFar = 0;
      const runtime = clockTime % totalScheduleDuration;
      for (const video2 of videos) {
        const { videoId, title } = video2;
        const durationSeconds = videoDuration(video2);
        if (videoDurationSoFar + SECONDS_BETWEEN_VIDEOS > runtime)
          return gap(videoId);
        videoDurationSoFar += SECONDS_BETWEEN_VIDEOS;
        if (videoDurationSoFar + durationSeconds > runtime) {
          return {
            type: "video",
            currentTime: runtime - videoDurationSoFar,
            videoId,
            videoTitle: title
          };
        }
        videoDurationSoFar += durationSeconds;
      }
      return gap("");
    },
    getName: () => name,
    getTotalDuration: () => totalScheduleDuration
  };
}
const add = (a, b) => a + b;
test("looping channel", {
  "plays the first episode after the second"() {
    const channel = LoopingChannel("Jonathan", [
      video("first", "The First", 100),
      video("second", "The Second", 100)
    ]);
    expect(channel.getBroadcast(250 * 1e3), equals, {
      videoId: "first",
      videoTitle: "The First",
      currentTime: 44,
      type: "video"
    });
  },
  "uses the provided name"() {
    const channel = LoopingChannel("Jonathan", []);
    expect(channel.getName(), is, "Jonathan");
  },
  "when no videos are provided, plays nothing"() {
    const channel = LoopingChannel("Jonathan", []);
    expect(channel.getBroadcast(42).type, is, "nothing");
  },
  "plays a first video at the beginning"() {
    const channel = LoopingChannel("Jonathan", [
      video("the-id", "the-title", 42)
    ]);
    expect(channel.getBroadcast(0 + SECONDS_BETWEEN_VIDEOS * 1e3), equals, {
      type: "video",
      videoId: "the-id",
      currentTime: 0,
      videoTitle: "the-title"
    });
  },
  "plays a video all the way though"() {
    const channel = LoopingChannel("Jonathan", [
      video("the-id", "the-title", 42)
    ]);
    expect(channel.getBroadcast(1e3 + SECONDS_BETWEEN_VIDEOS * 1e3), equals, {
      type: "video",
      videoId: "the-id",
      currentTime: 1,
      videoTitle: "the-title"
    });
  },
  "puts a gap between videos"() {
    const channel = LoopingChannel("Jonathan", [
      video("first", "The First", 100),
      video("second", "The Second", 10)
    ]);
    expect(channel.getBroadcast(103 * 1e3), equals, {
      type: "nothing",
      nextVideoId: "second"
    });
  },
  "plays a second episode after the first"() {
    const channel = LoopingChannel("Jonathan", [
      video("first", "The First", 1),
      video("second", "The Second", 1)
    ]);
    expect(channel.getBroadcast(2 * SECONDS_BETWEEN_VIDEOS * 1e3 + 1e3), equals, {
      type: "video",
      videoId: "second",
      currentTime: 0,
      videoTitle: "The Second"
    });
  }
});
function video(videoId, title, duration) {
  return { videoId, title, segments: [{ start: 0, end: duration }] };
}
